<template>
	<view class="video_play">
		<image :src="videoItem.img"></image>
		<!-- 工具栏 -->
		<view class="video_tool">
			<view :class="['iconfont',muted ? 'iconjingyin' : 'iconshengyin']" @click="controlAudio"></view>
			<view class="iconfont iconzhuanfa">
				<button open-type="share"></button>
			</view>
		</view>
		<!-- 工具栏 -->
		
		<!--视频 -->
		<view class="video_wrap">
			<video :muted="muted" :src="videoItem.video" controls="true" :poster="videoItem.img" objectFit="fill"></video>
		</view>
		<!--视频 -->
		
		<!-- 下载 -->
		<view class="download">
			<view class="download_btn" @click="download">下载高清</view>
		</view>
		<!-- 下载 -->
	</view>
</template>

<script>
	export default {
		data() {
			return {
				videoItem:{},
				muted:false
			}
		},
		onLoad() {
			this.videoItem = getApp().globalData.videoItem;
			console.log('video',this.videoItem);
		},
		methods: {
			controlAudio(){
				this.muted = !this.muted;
			},
			async download(){
				await uni.showLoading({
					title:'下载中....'
				});
				const {tempFilePath} = (await uni.downloadFile({url:this.videoItem.video}))[1];
				console.log(tempFilePath);
				
				await uni.saveVideoToPhotosAlbum({
					filePath:tempFilePath
				});
				
				uni.hideLoading();
				
				uni.showToast({
					title:'下载成功'
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.video_play{
		position: relative;
		image{
			position: absolute;
			width: 100vw;
			height: 100vh;
			z-index: -1;
			//滤镜
			filter: blur(20px);
		}
		.video_tool{
			width: 100%;
			height: 80rpx;
			display: flex;
			justify-content: flex-end;
			.iconfont{
				width: 80rpx;
				color: #FFFFFF;
				font-size: 50rpx;
				border-radius: 40rpx;
				background-color: rgba(0,0,0,.2);
				display: flex;
				align-items: center;
				justify-content: center;
				margin-right: 20rpx;
			}
			.iconzhuanfa{
				position: relative;
				button{
					position: absolute;
					width: 100%;
					height: 100%;
					opacity: 0;
				}
			}
		}
		.video_wrap{
			display: flex;
			justify-content: center;
			video{
				width: 540rpx;
				height: 900rpx;
			}
		}
		.download{
			display: flex;
			justify-content: center;
			margin-top: 30rpx;
			.download_btn{
				width: 360rpx;
				height: 80rpx;
				border-radius: 40rpx;
				display: flex;
				justify-content: center;
				align-items: center;
				color: #FFFFFF;
				border: 3rpx solid #FFFFFF;
				background-color: rgba(0,0,0,.5);
			}
		}
		
	}

</style>
